.cells {
    padding-top: 5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 3em;
    p {
        text-align: right;
        display: flex;
        justify-content: flex-end;
        flex-basis: 200px;
        flex-grow: 1;
        margin-left: 2em;
        font-size: 1.1em;
    }
}

.tags {
    display: flex;
    gap: 1em;
    margin: 3em 0;
    button {
        font-size: 1.5em;
        text-decoration: none;
        background: none;
        border: 2px solid ivory;
        border-radius: 99px;
        color: ivory;
        padding: 0.25em 1em;
        cursor: pointer;
        transition: background-color 0.15s linear, color 0.15s linear;
        &:hover {
            background-color: ivory;
            color: var(--black);
        }
    }
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 1em;
    margin: auto;
    justify-content: center;
    .craft {
        max-width: 90%;
        font-size: 0px; // somehow affects size despite there being no text
        height: min-content;
        position: relative;
        overflow: hidden;
        display: flex;
        flex-basis: 300px;
        flex-grow: 1;
        aspect-ratio: 1 / 1;
        background: var(--purple);
        
        img {
            object-fit: cover;
            width: 100%
        }
        .filter {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            background: linear-gradient(150deg, 
                rgba(0, 0, 0, 0),  
                rgba(100, 0, 58, 0.1), 
                rgba(50, 0, 58, 0.5), 
                var(--darkpurple), 
                var(--darkpurple));
            opacity: 0;
            transition: opacity .15s linear;

            &:hover {
                opacity: 1;

                .name{
                    opacity: 1;
                    right: 0.5em;
                }
            }

            .name {
                color: ivory;
                font-size: 2rem;
                opacity: 0;
                position: absolute;
                right: -0.5em;
                bottom: 0.5em;
                text-align: right;
                transition: opacity 0.15s linear, right 0.15s linear;
            }
        }
    }
}